*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  background-color: #ffffff;
  font: 14px/1 "Open Sans", Arial sans-serif;
  margin: 0;
  min-width: 320px;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
  margin-top: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;

  &:hover {
    color: inherit;
  }
}

.container {
  max-width: 1236px;
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overflow {
  overflow: hidden;
  height: 100vh;
  min-height: 460px;
}
